        "notes": "fHinweise:0fpsHeute sind Messfeiern nicht gestattet. psfHeute ist strenger Fast- und Abstinenztag0f (vgl. cann.1251fCIC und Hinweis Nr. 18 vor Aschermittwoch).psfLesehore und Laudes sollen am Karfreitag gemeinsam mit der Gemeinde in der Kirche gefeiert werden (Trauermette)0f (vgl.GL307v,1_90v); liturgische Farbe: violett.psZu Beginn der Liturgiefeier am Karfreitag sollen der Altar und Altarraum ganz leer sein, ohne Kreuze, Leuchter und Tcher.psDas in der Liturgiefeier am Karfreitag zur Kreuzenthllung verwendete Kreuz kann rot verhllt sein (nach rmischem Vorbild in St. Peter).psBei einer fGrablegungsfeier am Karfreitag0f kann das Kreuz oder eine Statue mitgetragen werden. Das Allerheiligste darf jedoch nicht im Heiligen Grab ausgesetzt werden (vgl. CE1998, Nr.331).psfDie hl. Kommunion0f darf nur whrend dieser Feier ausgeteilt werden. Den Kranken darf sie jedoch zu jeder Zeit gebracht werden.psfAm Karfreitag beginnt die Novene zur Gttlichen Barmherzigkeit0f zur Vorbereitung des Festes der gttlichen Barmherzigkeit am 2.Sonntag der Osterzeit (fBarmherzigkeitssonntag0f).psfEin vollkommener Ablass0f (s.o.Kap.I.C.10.) kann unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) gewonnen werden, wenn man die fLiturgie des Karfreitags0f andchtig mitfeiert und dabei an der fKreuzverehrung0f teilnimmt.p",
