        "notes": "fHinweise:0fpsIn der Osteroktav entfllt die Verpflichtung zum Freitagsopfer.psDas fFest des hl. Markus0f entfllt in diesem Jahr.psNach kirchlicher Tradition wird fvom Fest des hl. Markus bis zum Fest Kreuzerhhung (14.September) der Wettersegen fr das Gedeihen der Feldfrchte0f gebetet. In der Sorge um das tgliche Brot fr alle und als Ausdruck der gegenseitigen Frbitte soll der Wettersegen von den Gemeinden in Stadt und Land, vor allem in den lndlichen Gemeinden, gebetet werden (BenediktionaleNr.8, S.59ff, und MB566ff). Siehe auch die entsprechenden Hinweise vor der Bittwoche. In der Osteroktav bis zum Weien Sonntag sollte jedoch der FSS fr die Osterzeit den Vorrang behalten, ggf.auch an den Sonntagen der Osterzeit bis zum Hochfest Christi Himmelfahrt.p",
