        "notes": "fHinweise:0fpsfIm Commune fr Mrtyrer gibt es in der Osterzeit eigene Texte0f fr die 1. Lesung der Lesehore mit ihrem Responsorium, fr die Laudes und die Vesper sowie eigene Messformulare (MB907ff,Nr.8_10). In den brigen Formularen wird den Antiphonen zum Introitus und zur Kommunion ein Halleluja angefgt, sofern nicht die Antiphon schon ein Halleluja enthlt oder ihr Sinn dem widerstreitet.psAn jenen Wochentagen in der Osterzeit nach der Osteroktav, auf die kein Hochfest, Fest oder gebotener Gedenktag fllt, stehen fr die Messfeier folgende Formulare zur Wahl:pfMesse vom Wochentag0f; pfMesse eines/einer Heiligen0f, dessen/deren Gedchtnis vorgesehen oder der/die an diesem Tag im Martyrologium Romanum verzeichnet ist (AEM316b); pfMessen fr ein besonderes Anliegen oder Votivmessen0f nur dann, wenn eine echte Notwendigkeit besteht oder die pastorale Situation es erfordert; in diesem Fall auch an Ged (AEM333); pfMarien-Messen0f (MBm93_119) nur in Marienwallfahrtskirchen (die Erlaubnis wird jedoch nur fr auswrtige Priester gegeben oder sooft die Messe fr Pilger gefeiert wird), jedoch mit Tageslesungen (MBm,Past.Einf.31); peine fBegrbnismesse0f ist an allen Tagen mglich auer an den gebotenen Hochfesten und den Sonntagen der Osterzeit (AEM336); pfMessen fr Verstorbene0f nach Eintreffen der Todesnachricht und am ersten Jahrestag  auch an Ged mglich (AEM337).psWenn nicht anderes angegeben ist, wird whrend der Osterzeit in den Messfeiern immer eine der Osterprfationen I_V gewhlt.p",
