        "deceased_more": "1981 tAntonGoeb,Pfr.i.R.,OffizialsratE.h.,Fuldap2011 tDr.EduardKrieg,Dr.phil.,OStRi.R.,Prlat,Fuldap",
        "prayerTexts": "Beispieltext",
        "vig_notes": "fHOCHHEILIGES PFINGSTFEST0fjolfHinweise zum Hochfest und zur Vigilmesse:0fpsDie fVorabendmesse zu Pfingsten0f kann durch eine vermehrte Zahl der Schriftlesungen zu einer fPfingstvigil0f erweitert werden (HR,S.5_9/MBkl1228_1232). Diese Pfingstvigil kann auch als eigener Wortgottesdienst gehalten werden.psAus pastoralen Grnden knnen in der Vigilmesse auch die Perikopen vom Pfingstsonntag genommen werden.psfHeute endet die Zeit der Osterkommunion0f.psfEin vollkommener Ablass0f (siehe untenb+HINWEISE ZUM ABLASSlAblass ist der Nachlass zeitlicher Strafe vor Gott fr Snden, deren Schuld schon getilgt ist; lihn erlangt der entsprechend disponierte Glubige unter bestimmten festgelegten Voraussetzungen durch die Hilfe der Kirche, die im Dienst an der Erlsung den Schatz der Shneleistungen Christi und der Heiligen autoritativ verwaltet und zuwendet (can. 992 CIC). lJeder Glubige kann Teilablsse oder vollkommene Ablsse fr sich selbst gewinnen oder frbittweise Verstorbenen zuwenden (can. 994 CIC). lDie nach der Vergebung der Schuld noch verbleibenden Sndenstrafen knnen in diesem Leben durch Gebet und Opfer geshnt werden. Sie werden aber auch durch einen vollkommenen Ablass getilgt. lDen Verstorbenen, die in der Gnade Gottes aus diesem Leben scheiden, jedoch noch Sndenstrafen im Fegfeuer (Purgatorium, Reinigungsort) erleiden mssen, knnen wir frbittweise Ablsse zukommen lassen. lZur Gewinnung des Ablasses gehren: la) die allgemeinen Bedingungen: sakramentale Beichte mit entschlossener Abkehr von jeder Snde, Eucharistieempfang und Gebet nach der Meinung des Hl. Vaters (z.B. Vaterunser und Glaubensbekenntnis, oder ein anderes Gebet nach freier Wahl). Diese Bedingungen knnen erfllt werden auch mehrere Tage vor oder nach dem Ablasstag. Es muss aber jede Anhnglichkeit an eine Snde ausgeschlossen sein;lb) eine besondere Bedingung, die dem jeweiligen Ablasstag entspricht. lFehlt die volle Disposition oder bleibt eine der Bedingungen unerfllt, gewinnt man einen Teilablass (vgl. Enchiridion Indulgentiarum, 3. Aufl. 1986, pag. 73, n. 65).l0b+) kann unter den blichen Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet in den Anliegen des Papstes) gewonnen werden, wenn man heute Abend oder morgen in einer Kirche oder Kapelle andchtig dem feierlichen Gesang des fHymnus Veni, Creator Spiritus0f (GL341) bzw. fKomm, Heilger Geist0f (GL342) beiwohnt.p",
        "vig_liturgy": "olrtkabends:0k ggf. fVigilmesse von Pfingsten0f, oeGl,Cr, eigPrf und inden HgI_III eigEinschub wie morgen, FSS:MB546, Entlassungsruf:MB531bsf1.VpvomfolgH0f1.KpvomSopp"
