        "liturgy": "fSo0fatf16. Sonntag im Jahreskreis0flPsalt:IV.WocheTeDeumpgtfMs eig0f, Gl,Cr, SoPrf, FSSp",
        "notes": "fHinweise:0fpsfHeute wird hingewiesen auf den Portiunkulaablass0f, einen vollkommenen Ablass (s.o.Kap.I.C.10.), fder am 2. August oder am vorhergehenden oder am darauffolgenden Sonntag, jeweils vom Mittag des Vortages an, gewonnen werden kann0f. Er kann nur gewonnen werden in allen Pfarrkirchen und Pfarrkuratiekirchen sowie in den Kirchen und Kapellen, die dafr ein besonderes Indult besitzen, und in den Ordenskirchen der franziskanischen Ordensfamilien.  pfZur Gewinnung des Ablasses gehren:0f pa) fdie allgemeinen Bedingungen0f: Beichte mit entschlossener Abkehr von jeder Snde, Kommunionempfang und Gebet nach der Meinung des Hl. Vaters (z.B. Vaterunser und Glaubensbekenntnis oder ein anderes Gebet nach freier Wahl);pb) fals besondere Bedingung0f: ein Kirchenbesuch mit Gebet (Vaterunser und Glaubensbekenntnis). pDieser Ablass kann auch den Verstorbenen zugewendet werden. Er kann aber nur einmal an diesem Ablasstag gewonnen werden.psDie fGedenktage der hl. Margareta0f und fdes hl. Apollinaris0f entfallen in diesem Jahr.p",
