        "liturgy": "fDi0ftkA. Auerhalb der Stadt Fulda:0kpwtfhll. Marta, Maria und Lazarus von Betanien0f, fGed0f BuMeigbewfMs0f: HR/ComMFr1+4+5, PrfHllIoderII, Erffnungs- und Kommunionvers wie bisher am 29.JulibsfLundEv0f wie bisher am 29.Juli (MLV749/773), feigEv0f0wprtOder fhll. Simplicius, Faustinus und Beatrix0f, Mrtt, fGed0f(FD)bfwfMs0f: FDM 35ff  fLundEv0f:FDL18ff0wpw/rtfOff und Ms0f von einem der GedplkB. In allen Kirchen und Gemeinden derStadtFulda:0klfHochfest der hll. Simplicius, Faustinus und Beatrix0f, Mrtt, Stadtpatrone (FD)lfOff0f Com Mrtt, eig 2. L, Resp und Or, TeDeum  Kl.Hore:AntausdemCom,ErgPspr        tfMs eig0f w(FDM35_37,FDL18_21)0w, Gl,Cr, fFrbitten fr Fulda0f, PrfHlloderMrtt, FSS:MB560  f2.VpvomH0ff2.KpvomSo0fp",
        "notes": "fHinweise:0fpsIn allen Kirchen und Gemeinden der Stadt Fulda wird heute das Hochfest der hll. Stadtpatrone Simplicius, Faustinus und Beatrix gefeiert. Besondere Frbitten in den Hochfestmessen der Gemeinden und Kirchen Fuldas fr alle, die in Fulda leben, arbeiten und sich aufhalten.psNutzer der Stundenbuch-App des DLI finden die vollstndigen fTexte fr das Stundengebet zur heutigen Eigenfeier des Dizesankalenders als Hochfest in der Datei FD-Stundengebet.PDF0f (s.o.S.87, Nr.3).psHeute kann auerhalb Fuldas der neue ferweiterte Gedenktag der hll. Marta, Maria und Lazarus0f gefeiert werden. Er wurde von Papst Franziskus am 26. Januar 2021 in den Rmischen Generalkalender eingefgt. Im Haus von Bethanien erlebte der Herr Jesus den Familiensinn und die Freundschaft von Marta, Maria und Lazarus; deshalb heit es im Johannesevangelium, dass er sie liebte. Marta nahm ihn in Gastfreundschaft grozgig auf, Maria hrte aufmerksam auf sein Wort, und Lazarus stieg auf den Befehl dessen, der den Tod erniedrigt hatte, sogleich aus dem Grab. Die Unsicherheit, die in der berlieferung der lateinischen Kirche bezglich der Identitt Marias  der Magdalena, der Christus nach seiner Auferstehung erschien, der Schwester der Marta und der Snderin, deren Snden der Herr vergeben hatte  bestand und die den Ausschlag dafr gab, dass allein Marta am 29. Juli im Rmischen Kalender verehrt wird, ist in neueren Studien und Zeiten gelst worden, wie das erneuerte Rmische Martyrologium bezeugt, das an diesem Tag auch Marias und Lazarus gedenkt. Auch in einigen partikularen Kalendern werden die drei Geschwister an diesem Tag gefeiert. In Anbetracht des wichtigen Zeugnisses fr das Evangelium, das sie gaben, indem sie den Herrn Jesus in ihrem Haus aufnahmen, ihm aufmerksam zuhrten und glaubten, dass er die Auferstehung und das Leben ist, hat Papst Franziskus deshalb auf Vorschlag dieses Dikasteriums (Kongregation fr den Gottesdienst und die Sakramentenordnung) angeordnet, dass der Gedenktag der Heiligen Marta, Maria und Lazarus am 29. Juli in den Rmischen Generalkalender aufgenommen wird.p",
