        "notes": "fHinweise:0fpsfDer Gedenktag des hl. Maximilian Maria Kolbe0f (im RK und GK am 14. Aug.) wird im Bistum Fulda fam 17. August0f gefeiert, entfllt aber in diesem Jahr.psDas Weihefest der Fuldaer Kathedrale wird fim Dom zu Fulda als Hochfest der eigenen Kirchweihe0f begangen; alles wie unten angegeben, auer: Kl.Hore:Antaus dem Com, ErgPs  Ms aus dem Com Kirchweihe A (MB878/879) mit GlundCr, in den HgI_III eigEinschub.psNutzer der Stundenbuch-App des DLI finden die fTexte fr das Stundengebet zur heutigen Eigenfeier des Dizesankalenders in der Datei FD-Stundengebet.PDF0f (s.o.S.87, Nr.3).p",
