        "prayerTexts": "Beispieltext",
        "vig_notes": "olfHinweise zum Hochfest und zur Vigilmesse:0fpsfDas Hochfest der Aufnahme Mariens in den Himmel ist im Bistum Fulda kirchlich gebotener Feiertag0f. Es besteht fApplikations- sowie Sonn- und Feiertagspflicht0f (siehe untenb+HINWEISE ZUR SONN- UND FEIERTAGSPFLICHTlAn den kirchlich gebotenen Feiertagen gilt fr die Glubigen die Pflicht zur Teilnahme an einer Messfeier (die am Tag oder Vorabend erfllt werden kann) und zur Enthaltung von dem Sonn- bzw. Feiertag nicht gemer Arbeit (Halten der Sonn- und Feiertagsruhe) (vgl. cann. 1247f CIC; KKK 21802185 und 20412043). lMessfeiern, die im Radio oder Fernsehen bertragen werden, sind kein Ersatz fr solche Messfeiern, die von den Glubigen in rumlicher Gegenwart mitzufeiern sind (Partikularnorm Nr. 8 der DBK, Abschnitt 4, S. 81, in: KA 1995, Nr. 153).llHINWEISE ZUR APPLIKATIONSPFLICHTlFr alle Pfarrer und Inhaber einer Pfarrkuratie sowie die Pfarradministratoren gilt an diesen Tagen die Applikationspflicht, die darin besteht, eine Heilige Messe fr die lebenden Glieder ihrer Gemeinden zu applizieren. lPfarrer usw., die eine andere Pfarrei oder Pfarrkuratie mitverwalten, knnen durch eine und dieselbe Heilige Messe der Applikationspflicht fr alle ihnen anvertrauten Pfarreien (Pfarrkuratien) gengen. lWenn ein Pfarrer an einem Applikationstag rechtmig verhindert ist, kann er die Applikationsmesse entweder von einem anderen Priester am Tage selbst halten lassen oder sie selbst an einem anderen Tag halten. Unterlassene Applikationsmessen mssen baldmglichst in voller Anzahl nachgeholt werden (vgl. cann. 388; 534; 543 CIC). 0b+).psAus pastoralen Grnden knnen in der fVigilmesse vom Hochfest der Aufnahme Mariens in den Himmel0f auch die Perikopen der Tagesmesse genommen werden.psZur fKrutersegnung0f s.BenediktionaleNr.9,S.63ff.psAm Schluss der Komplet kann heute und morgen passenderweise die fMarianische Antiphon Ave Regina caelorum0f (GL666v,20v) bzw. fAve, du Himmelsknigin0f (vgl.GL868) gewhlt werden, n.B. auch in der gesamten Oktav bis zum Ged Maria Knigin am 22.August.psWegen des Hochfestes entfllt morgen die Verpflichtung zum Freitagsopfer.p",
        "vig_liturgy": "olwtkabends:0k ggf. fVigilmesse von der Aufnahme Mariens in den Himmel0f, oeGl,Cr, eigPrf und inden HgI_III eigEinschub wie morgen, FSS:MB554bsf1.VpvomfolgH0ff1.KpvomSo0fpp"
