        "notes": "fHinweise:0fpsEin fkumenischer Schpfungstag0f wurde beim 2.kumenischen Kirchentag im Mai2010 in Mnchen feierlich deklariert und von den Mitgliedskirchen der Arbeitsgemeinschaft Christlicher Kirchen in Deutschland (ACK) offiziell auf den f1.Freitag im September0f gelegt. Je nach lokalen und regionalen Gegebenheiten knnen Gemeinden den kumenischen Schpfungstag auch an einem anderen Termin der fSchpfungszeit zwischen dem 1.September und dem 4.Oktober0f feiern. fBesondere Frbitten und/oder Gottesdienste im Anliegen der Bewahrung der Schpfung und des Klimaschutzes.0f Eine Arbeitshilfe zur Anregung fr Gemeinden, in gemeinsamer kumenischer Initiative die Schpfung zu feiern, kann bei der ACK  kumenische Centrale, www.oekumene-ack.de oder www.schoepfungstag.info, bestellt werden.  Zur kumenischen Schpfungszeit eignen sich besonders die Lieder GL463 und466.psHeute wird in Wrdigung des Wirkens der hl. Mutter Teresa der von den Vereinten Nationen am 17. Dezember 2012 ausgerufene Internationale Welttag der Nchstenliebe begangen.psfZumHerz-Jesu-Fr:0f Die Votivmesse ist erlaubt.p",
