        "notes": "fHinweise:0fpsDie Messfeier zum 31. Sonntag im Jkr. entfllt in diesem Jahr.psfDas Hochfest der Kirchweihe als Sollemnitas externa am allgemeinen Kirchweihsonntag0f (1.Sonntag im November) kann in diesem Jahr fheute nicht, aber am kommenden Sonntag, dem 9. November0f, gefeiert werden. Es kann auch am kommenden Sonntag das Gedchtnis und die Feier der eigenen Kirchweihe mit der Feier der Liturgie des Weihefestes der Lateranbasilika auf geeignete Weise mitbedacht und verknpft werden.psfAn Allerseelen darf jeder Priester dreimal zelebrieren.0f Dabei darf er fr die 2. und 3.Messe ein Stipendium annehmen, das ungekrzt an den Generalvorstand des Bonifatiuswerkes abzufhren ist. Wer fr die 2. und/oder 3.Messe kein Stipendium hat, mge in der Intention des Bonifatiuswerkes zelebrieren und dies dorthin melden. Wer von diesen Mglichkeiten keinen Gebrauch macht, muss, wenn er mehrmals zelebriert, seine 2.Messe fr alle verstorbenen Christglubigen, die 3.nach der Meinung des Papstes applizieren.psfAn Allerseelen0f empfiehlt es sich, bei den Messfeiern oder bei Andachten fr die Verstorbenen die fOsterkerze im Altarraum0f aufzustellen und zu entznden.psAllerseelen steht liturgisch im Rang eines Hochfestes, ohne jedoch Hochfest zu sein (s.o.Kap.I.B.1.I.3.).p",
