        "notes": "fHinweise:0fpsfHeute ist der 9. Welttag der Armen.0f Zum Ende des Heiligen Jahres der Barmherzigkeit hat Papst Franziskus mit dem Apostolischen Schreiben Misericordia et misera vom 21.November2016 unter Kapitel21 den 33.Sonntag im Jahreskreis zum neuen Welttag der Armen erklrt und dazu geschrieben: Das wird die wrdigste Vorbereitung fr die Feier des Christknigssonntags sein, denn Jesus Christus hat sich mit den Geringen und Armen identifiziert und wird uns nach den Werken der Barmherzigkeit richten (vgl.Mt25v,31460v). Es wird ein Tag sein, der den Gemeinden und jedem Getauften hilft, darber nachzudenken, wie die Armut ein Herzensanliegen des Evangeliums ist und dass es keine Gerechtigkeit noch sozialen Frieden geben kann, solange Lazarus vor der Tr unseres Hauses liegt (vgl.Lk16v,19210v). Dieser Tag wird auch eine echte Form der Neuevangelisierung darstellen (vgl.Mt11v,50v), durch die das Antlitz der Kirche in ihrer stndigen pastoralen Umkehr erneuert wird, um Zeugin der Barmherzigkeit zu sein.psfHeute ist Volkstrauertag.0f fBesondere Frbitten0f fr die Opfer und Verursacher der Kriege, von Gewalt und Terror in Vergangenheit und Gegenwart.psfJugendliche mgen besonders zur Feier des Christknigssonntags und Weltjugendtages am kommenden Sonntag eingeladen werden.0f Papst Franziskus hat am Christknigssonntag, dem 22.November2020, die dizesanen Weltjugendtage ab 2021 vom bisherigen Palmsonntag auf das Christknigshochfest verlegt.psfHeute0f ist der fDiasporasonntag0f.  fBesondere Frbitten0f fr die Christen in der Zerstreuung.psDer fGedenktag der hl. Margareta0f entfllt in diesem Jahr.p",
