        "notes": "fHinweise:0fpsfHeute Vormittag ist im Dom zu Fulda Chrisammesse0f (lweihemesse) mit eig Formular, Gl, eig Prf, Weihe der le, Farbe: wei. fDiese Messe mit der Erneuerung der Weiheversprechen der Priester feiert der Bischof in Konzelebration mit seinem Priesterkollegium.0f Sie soll ein Ausdruck der Verbundenheit zwischen dem Bischof und seinen Priestern sein. Daher empfiehlt es sich, fdass alle Priester nach Mglichkeit an dieser Messe teilnehmen.0f Zum Zeichen der Einheit sollen Priester aus allen Regionen des Bistums konzelebrieren (vgl.MBI[15], MBO339) und unter beiden Gestalten kommunizieren. Wer in der Chrisammesse (kon)zelebriert hat, darf auch in der Abendmahlsmesse (kon)zelebrieren. ber zweimalige Kommunion  s.o.Kap.III.C.16c.psDie fKommemoration des hl. Liudger0f entfllt in diesem Jahr.p",
