        "notes": "fHinweise:0fpsfDie Sonntage der Osterzeit0f sollen in ihrer festlichen Gestaltung das sterliche 50-Tage-Fest zum Ausdruck bringen. Die vorgesehenen Messtexte drfen nicht ersetzt werden bspw. durch Texte einer Messfeier, in der die Taufe, die Firmung oder ein anderes Sakrament bzw. Sakramentale gespendet werden (AEM330).psfDas sonntgliche Taufgedchtnis0f (bis Pfingsten mit Vidi aquam, GL125) soll an die Stelle des Allgemeinen Schuldbekenntnisses treten (Segnung und Austeilung des Weihwassers sowie Vergebungsbitte, MB1171/1207).psfHeute ist der Sonntag der Gttlichen Barmherzigkeit0f (eingefhrt am 30.April2000). In den liturgischen Bchern ist die Bezeichnung Sonntag der Gttlichen Barmherzigkeit zu ergnzen.psAm Barmherzigkeitssonntag mge das Bild des Barmherzigen Jesus im Altarraum aufgestellt und verehrt und von allen Priestern die gttliche Barmherzigkeit verkndet werden.psMit der Feier des Barmherzigkeitssonntags hat der hl. Papst Johannes Paul II. 2002 einen fvollkommenen Ablass0f verbunden (s.o.Kap.I.C.10.). Er wird unter den gewohnten Bedingungen (Beichte, entschlossene Abkehr von jeder Snde, Kommunionempfang, Gebet nach der Meinung des Hl. Vaters) den Glubigen gewhrt, die in einer Kirche oder einem Oratorium fan einer Feier zu Ehren der Gttlichen Barmherzigkeit teilnehmen0f oder wenigstens vor dem Allerheiligsten fdas Vaterunser und das Glaubensbekenntnis mit dem Zusatz einer kurzen Anrufung des barmherzigen Herrn Jesus (z.B. Barmherziger Jesus, ich vertraue auf dich!) beten0f.psfDie Vorabendmesse zum Hochfest der Verkndigung des Herrn kann heute nicht gefeiert werden.0fpsDer fGedenktag des hl. Johannes Baptist de la Salle0f entfllt in diesem Jahr.p",
