        "notes": "fHinweis:0f sMit Dekret der KGS vom 3. Juni 2016 (Prot. N. 257/16) hat Papst Franziskus fden bisherigen gebotenen Gedenktag der hl. Maria Magdalena zum Fest erhoben und damit das liturgische Gedchtnis dieser Heiligen dem liturgischen Rang der Apostelfeste0f (ausgenommen das Hochfest Peter und Paul) fgleichgestellt aufgrund ihrer Eigenschaft als Apostelin der Apostel0f. In den liturgischen Bchern (MBund MBkl, MLV, Schott-Messbuch fr die Wochentage II, StBIII und Lektionare zum StB I/6 und II/6) ist daher zur Feier des Festes der hl. Maria Magdalena fdie Bezeichnung Apostelin der Apostel zu ergnzen und der Rang Gedenktag durch Fest zu ersetzen0f, in den Messbchern fGloria0f und in den Lektionaren zum StB nach dem Responsorium zur 2. Lesung fTeDeum0f einzufgen.p",
