        "notes": "fHinweise:0fpsHeute wird zum 4. Mal der fWelttag der Groeltern und Senioren0f begangen. Papst Franziskus hat ihn am 31. Januar 2021 ausgerufen und  in zeitliche Nhe zum Gedenktag der hll. Joachim und Anna, der Groeltern Jesu  auf den 4. Sonntag im Juli gelegt. rIn seiner Angelus-Ansprache am 31. Januar 2021 sagte er:  Der Heilige Geist weckt auch heute noch in den alten Menschen Gedanken und Worte der Weisheit: Ihre Stimme ist kostbar, weil sie das Lob Gottes singt und die Wurzeln der Vlker bewahrt. Sie erinnern uns daran, dass das Alter ein Geschenk ist und dass die Groeltern das Bindeglied zwischen den Generationen sind, um die Lebens- und Glaubenserfahrung an die Jugend weiterzugeben. ... Es ist wichtig, dass sich die Groeltern mit den Enkeln treffen und die Enkel mit den Groeltern, denn  wie der Prophet Jol sagt  die Groeltern werden vor den Enkeln trumen, Illusionen [groe Wnsche] haben, und die jungen Leute, die von ihren Groeltern gestrkt werden, sie werden vorwrts gehen, sie werden prophezeien.  0rZur Feier des Welttages mgen das jhrliche Leitwort und verffentlichte Materialien (Vatikan, DBK) bercksichtigt werden.psAm heutigen Sonntag kann vom Mittag des Vortages an der fPortiunkulaablass0f gewonnen werden, auch am 2. August oder dem darauffolgenden Sonntag vom Mittag des Vortages an (vgl. Hinweis vor dem 21.Juli).p",
