        "notes": "fHinweise:0fpsHeute am fTag der deutschen Einheit0f mge in den Frbitten auch fr diese Anliegen gebetet werden. Es kann eine geeignete Votivmesse gewhlt werden (siehe unten). Auch kumenische Gottesdienste oder ggf.Gebetstreffen der Religionen und gemeinsame Begegnungen eignen sich fr diesen Tag. Vgl. hierzu: Arbeitshilfe Nr. 170 der DBK: Leitlinien fr das Gebet bei Treffen von Christen, Juden und Muslimen. Eine Handreichung der deutschen Bischfe. 2., berarbeitete und aktualisierte Auflage 24. Juni 2008.psHeute am fTag der Offenen Moschee0f der Muslime in Deutschland mge in den Frbitten auch fr respektvolle Begegnungen, den notwendigen und aufrichtigen Dialog und das ehrliche und gute Zusammenwirken von Christen und Muslimen fr den Schutz und die Frderung von sozialer Gerechtigkeit, der sittlichen Gter sowie des Friedens und der (Religions-)Freiheit fr alle Menschen gebetet werden.psfZum Priesterdonnerstag (monatl. Gebetstag fr geistliche Berufe):0f Die Votivmesse ist erlaubt.  Frbitte(n) um Priesterberufungen.  Thema: Dochich, ich wei: Mein Erlser lebt. (Ijob19v,250v). Intention: Ordensleute.p",
