        "notes": "fHinweise:0fpsfAn Allerseelen darf jeder Priester dreimal zelebrieren.0f Dabei darf er fr die 2. und 3.Messe ein Stipendium annehmen, das ungekrzt an den Generalvorstand des Bonifatiuswerkes abzufhren ist. Wer fr die 2. und/oder 3.Messe kein Stipendium hat, mge in der Intention des Bonifatiuswerkes zelebrieren und dies dorthin melden. Wer von diesen Mglichkeiten keinen Gebrauch macht, muss, wenn er mehrmals zelebriert, seine 2.Messe fr alle verstorbenen Christglubigen, die 3.nach der Meinung des Papstes applizieren.psfAn Allerseelen0f empfiehlt es sich, bei den Messfeiern oder bei Andachten fr die Verstorbenen die fOsterkerze im Altarraum0f aufzustellen und zu entznden.psAllerseelen steht liturgisch im Rang eines Hochfestes, ohne jedoch Hochfest zu sein (s.o.Kap.I.B.1.I.3.).psfDie Vorabendmesse zum 31. Sonntag im Jkr. bzw. zum Hochfest der Kirchweihe kann heute nicht gefeiert werden0f, da Allerseelen in der Rangordnung der liturgischen Tage hher steht als die Sonntage im Jkr. und das Hochfest der Kirchweihe (s.o.Kap.I.B.1.I.+II.). fHeutige Abendmessen sind Messfeiern von Allerseelen.0f Dies bitte in den Gottesdienstordnungen so verffentlichen.psfZum Herz-Mari-Sa:0f Die Votivmesse ist nicht erlaubt.p",
