        "notes": "fHinweise:0fpsfDas Fest des hl. Stephanus ist als 2. Weihnachtsfeiertag kirchlich gebotener Feiertag0f. Es besteht fApplikations- sowie Sonn- und Feiertagspflicht0f (s.o.Kap.I.C.1.). In den Messfeiern wird daher das Glaubensbekenntnis gebetet (bzw. gesungen).psSeit dem Jahr 2003 trgt das Fest des hl. Stephanus in Deutschland auch den Charakter eines Gebetstages der Solidaritt mit den verfolgten und bedrngten Christen aufgrund der Initiative der DBK in diesem Anliegen. Durch Beschluss der DBK vom Juni 2012 wurde er ab 2012 zum fGebetstag fr verfolgte und bedrngte Christen0f erklrt. Spezielle Frbitten der DBK zum heutigen Gebetstag auf der Homepage der DBK www.dbk.de unter: Men  Themen  Solidaritt mit verfolgten Christen  Gebetstag.p",
