        "notes": "fHinweise:0fpsAm heutigen fFest der hl. Familie0f wird der fFamiliensonntag0f begangen. fLeitthema und Arbeitshilfen unter www.ehe-familie-kirche.de0f. Dizesen, Gemeinden, Verbnde und kirchliche Einrichtungen sind eingeladen, sich mit eigenen Veranstaltungen und Initiativen zum Jahresthema einzubringen und das Jahresmotto ggf.an einem anderen Sonntag im Kirchenjahr aufzugreifen.psZum heutigen fFest der hl. Familie0f ist es angebracht, eine ffeierliche Segnung der Kinder0f zu halten. Ein Vorschlag findet sich im BenediktionaleNr.2,S.34ff. Es knnen aber auch Familien oder Familienteile gesegnet werden, etwa nach der Homilie oder im Anschluss an die hl. Messe (vgl. BenediktionaleNr.51,S.237ff: Segnung einer Familie durch den Priester).   Frbitten fr die Familien.psDie fKomm des hl. Thomas Becket0f entfllt in diesem Jahr.p",
